@import '../css/variables.scss';

.container {
  width: 100%;
  min-width: $content_width;
  height: 84px;
  width: 100%;
  position: absolute;
  bottom: 30px;
  left: 0px;
  display: flex;
  justify-content: center;
  transition: padding-left 0.3s;
  #disabled {
    > input:disabled {
      &::placeholder {
        color: #c9c9c9;
      }
    }
  }
  .input_div {
    width: $content_width;
    position: relative;
    input {
      font-size: 16px;
      margin-bottom: 12px;
      width: 100%;
      padding: 16px;
      border-radius: 41px;
      border: 1px solid #c9c9c9;
      outline: none;
      &:focus {
        border: 1px solid #b5e7a8;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.12);
      }
    }
    .text_length {
      position: absolute;
      height: 51px;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #7f7f7f;
      right: 50px;
    }
    > span {
      position: absolute;
      right: 15px;
      height: 51px;
      display: flex;
      align-items: center;
      font-size: 24px;
      cursor: pointer;
    }
    > p {
      color: #a5a5a5;
      font-size: 12px;
      text-align: center;
    }
  }
}